.formulario {

    max-width: 100%; /* Establece un ancho máximo para el formulario */
    margin: 0 auto; /* Centra el formulario en la página */

    #descripcion {
        width: 100%; /* Asegura que el textarea ocupe todo el ancho disponible */
        box-sizing: border-box; /* Incluye el padding y borde en el ancho total */
        resize: vertical; /* Permite ajustar la altura verticalmente */
    }

    fieldset {
        border-radius: 1rem;
    }
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: $grisOscuro;
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
}