.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error {
        background-color: $rojo;
        border-radius: 1rem;
        width: 80%;
        
    }

    &.exito {
        background-color: $verde;
        border-radius: 1rem;
        width: 80%;
    }
}

.contenedor-centrado {
    display: grid;
    place-items: center;
}


table.propiedades {
    align-items: flex-end;
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    border-radius: .7rem;
    -webkit-border-radius: .7rem;
    overflow: hidden;
    border: 1px solid $verde;

    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }

    tbody {
        text-align: center;

        td {
            border: 1px solid $gris;

            a {
                margin: 1rem;
                border-radius: 1rem;
            }

            img {
                margin: 0 auto;
            }
        }

        tr:nth-of-type(odd) {
            background-color: #f5f1f1;
        }
    }

    .imagen-tabla {
        width: 10rem;
    }
}

.imagen-small {
    width: 20rem;
}

@media (max-width: $telefono) {
    table.propiedades {
        display: block;
        overflow-x: auto;
        
    }
}

.contenedor-nuevo {
    @media (max-width: $telefono) {
        display: grid;
        place-items: center;
    }
}