.boton-amarillo {
    @include boton($amarillo, inline-block);
    border-radius: 1rem;
    @media (max-width: $telefono) {
        width: 90%;
    }
}

.boton-amarillo-block {
    @include boton($amarillo, block);
    border-radius: 1rem;
}

.boton-verde {
    @include boton($verde, inline-block);
    border-radius: 1rem;
    margin-bottom: 1rem;
    @media (max-width: $telefono) {
        width: 90%;
    }
}

.boton-verde-small {
    @include boton($verde, inline-block);
    border-radius: 1rem;
}

.boton-rojo-block {
    @include boton($rojo, block);
}

.boton-rojo {
    @include boton($rojo, inline-block);
    border-radius: 1rem;
}

.boton-verde-block {
    @include boton($verde, block);
}

.contenedor-botones {
    .boton-amarillo {
        width: 15rem;
        height: 4.8rem;
    }
    .boton-rojo {
        width: 15rem;
        height: 4.8rem;
    }
}